footer {
  overflow-x: hidden;
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: 80%;
  padding-bottom: 5%;
  background: #191a1f;
  min-width: 100%;
  height: 100%;
}

.footer-container-main {
   padding-top: 170px;
}

a {
  text-decoration: none;
  color: rgb(266, 266, 266);
}

li {
  list-style: none;
}

.icons {
  display: flex;
  justify-content: space-around;
  max-width: 10%;
}

.icons {
  padding-top: 30px;
  font-size: 2.5rem;
  padding-left: 70px;
}

.icons li {
  padding-left: 20px;
}

.about {
  color: rgb(266, 266, 266);
  padding-left: 30px;
  padding-top: 30px;
}

.about p {
  max-width: 40%;
}

.links {
  display: flex;
  max-width: 120%;
  justify-content: space-around;
  color: rgb(236, 235, 235);
  max-height: 100%;
}

.links a {
  color: rgb(218, 213, 213);
  letter-spacing: 0.5px;
}

.links h2 {
  padding-bottom: 20px;
}

.product {
  padding-right: 100px;
}

.resources {
  padding-left: 80px;
}

.icons .fa-discord{
  transition: all ease-in-out 1s;
}

.icons .fa-github{
  transition: all ease-in-out 1s;
}

.icons .fa-discord:hover, .fa-github:hover{
  filter: brightness(70%);
  transition: all ease-in-out 1s;
}

footer a:hover{
  filter: brightness(70%);
  transition: all ease-in-out 1s;
}

@media screen and (max-width: 1150px) {
  .links{
    padding-right: 30px;
  }
}

@media screen and (max-width: 930px) {
  footer {
    flex-direction: column;
  }

  .icons {
    padding-bottom: 40px;
  }

  .about p {
    max-width: 80%;
  }

  .links{
    padding-right: 0px;
  }
}

@media screen and (max-width: 447px) {
  .links {
    flex-direction: column;
  }

  .product {
    padding-bottom: 40px;
    padding-left: 0;
    padding-right: 0;
  }
  .resources {
    padding: 0;
  }
}

a:hover {
    cursor: pointer;
}
